<template>
  <div class="remote-work-monitoring-style">
    <div class="wd-style">
      <div class="pic-data">
        <el-date-picker
          v-model="dateValue"
          align="right"
          type="date"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
          :clearable="false"
          placeholder="选择日期"
        ></el-date-picker>
      </div>
    </div>
    <div class="remote-work-mon-top" v-if="resStatus">
      <div class="top-back-gauge">
        <remote-left></remote-left>
      </div>
      <div class="top-middle">
        <remote-map></remote-map>
      </div>
      <div class="top-right">
        <remote-right></remote-right>
      </div>
    </div>
    <div class="remote-center" v-if="resStatus">
      <remore-center></remore-center>
    </div>
  </div>
</template>

<script>
// import remoteWorkMap from './remoteWorkMap'
import remoteMap from './components/remoteWorkMap';
import remoteLeft from './components/remoteLeft'
import remoteRight from './components/remoteRight'
import remoreCenter from './components/remoreCenter';

export default {
  name: "ycjk",
  components: {
    remoteMap,
    remoteLeft,
    remoteRight,
    remoreCenter,
  },
  data() {
    return {
      dateValue: null,
      resStatus: true,
      dateParams: ''
    }
  },
  watch: {
    dateValue(v) {
      let str = v.replace(/-/g, '');
      this.dateParams = str;
      window.console.log(this.dateParams, ' this.dateParams');
      this.resStatus = false;
      this.$nextTick(() => {
        this.resStatus = true;
      })
    }
  },
  created() {

  },
  mounted() {
    this.dateValue = this.$parent.date;
  },
  methods: {}

}
</script>

<style scoped lang="scss">
.remote-work-monitoring-style {
  width: 100%;
  // width: calc(100%-140px);
  height: 100%;
  padding: 0 70px;
  box-sizing: border-box;
  position: relative;

  // .top-wd {

  //   width: 100%;
  //   height: 40px;
  //   z-index: 9;

  .wd-style {
    width: 9%;
