    padding: 10px 40px;
    // border: 1px solid #21a8ae;
    // box-shadow: #21a8ae 0px 0px 10px inset;
    display: flex;
    justify-content: center;

    span {
      color: #b7f4ff;
      /* padding: 6px 8px; */
      border: 1px solid #b7f4ff;
      border-radius: 50%;
      font-size: 12px;
      width: 30px;
      height: 30px;
      float: left;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }

    span.active {
      background: rgba(183, 224, 225, 0.4);
      color: #fff;
    }

    .line-style {
      border-top: 1px solid #b7f4ff;
      height: 10px;
      width: 52px;
      float: left;
      margin-top: 15px;
    }

    .sjx-left {
      width: 0;
      height: 0;
      border-bottom: 60px solid rgba(33, 168, 174, 0.2);
      border-left: 50px solid transparent;
      position: absolute;
      left: -50px;
      top: 0px;
    }

    .label-icon {
      margin-left: 10px;
      padding: 7px;
      cursor: pointer;

      i {
        color: #fff;
        font-size: 16px;
      }
    }

    .pic-data {
      width: 100%;
      float: left;
      margin-left: 10px;
      background-image: linear-gradient(
        270deg,
        rgba(0, 120, 255, 0.4) 0%,
        rgba(0, 24, 77, 0.5) 19%,
        rgba(0, 23, 77, 0.5) 82%,
        rgba(0, 120, 255, 0.4) 100%
      );
      .el-input {
        width: 100%;
        box-sizing: border-box;
        border: 1px solid rgb(1, 70, 138);
        // border-radius: 20px;
        border-radius: 4px;
        /deep/ .el-input__inner {
          // border: 1px solid #011d5c;
          background: transparent !important;
          margin: 0;
          border-color: transparent;
          color: #fff;
          font-size: 16px;
        }
        /deep/ .el-input__prefix {
          left: 85%;
        }
        /deep/ .el-input__icon {
          font-size: 16px;

          color: #00e7ff;
        }
      }
      // }
    }
  }

  .remote-work-mon-top {
    width: 100%;
    height: calc(100% - 15px);
