      flex: 1;
      float: left;
      height: 100%;
      position: relative;
    }

    .top-right {
      flex: 1;
      // border: 1px solid #1354ce;
    }
  }

  .remote-center {
    width: 12.5%;
    height: 45%;
    position: absolute;
