<template>
  <div class="top-left-style">
    <div
      class="main-style"
      v-loading="topLoading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="加载中"
    >
      <div class="left-details">
        <div class="details-inline" v-for="(item, i) in tdMap" :key="i">
          <div class="inline-title">{{ item.name }}</div>
          <div class="inline-main">
            <div class="main-info" v-for="(v, k, z) in item.data" :key="z">
              <div class="keyInfo">{{ k }}</div>
              <div class="valueInfo">{{ v }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--        <div class="main-style">-->
    <!--            <div class="left-title">-->
    <!--                临时播报-->
    <!--            </div>-->
    <!--            <div class="left-details">-->

    <!--            </div>-->
    <!--        </div>-->
  </div>
</template>

<script>
import Interface from '../../../base/mixin/remote-work-monitoring';
import { dataChange, translateFilter } from '../../../commonJS/dataChanges'
import qs from 'qs'

export default {
  name: "remoreCenter",
  mixins: [Interface],
  data() {
    return {
      translateMap: {
        '01': '计划停电',
        '02': '故障停电',
        // '02': '电网故障停限电',
        '03': '违约停电',
        '04': '窃电停电',
        '05': '欠费停电',
        '06': '有序用电停电',
        '07': '临时停电',
        '99': '其他停电',
      },
      statusMap: {
        '01': '全部送电', '02': '部分送电', '03': '未送电', '04': '正执行'
        // '01': '已送电', '02': '未执行', '03': '取消计划', '04': '正执行'
      },
      tdMap: [],
      topLoading: true
    }
  },
  watch: {},
  created() {
    this.getBlackoutReleaseMonitoring(qs.stringify({
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.topLoading = false;
      this.dataFilter(res);
    }).catch(() => {
      this.topLoading = false;
    })
  },
  mounted() {
  },
  methods: {
    dataFilter(res) {
      let data = translateFilter(res, this.translateMap, 'typecode', 'typeName');
      data = translateFilter(data, this.statusMap, 'status', 'statusName')
      let _info = dataChange(data, 'typecode', 'typeName');
      window.console.log(data, _info, '_info');
      let tdMap = [], includesCodes = [];
      let codes = ['01', '07', '02', '99'], status = ['03', '02', '01'];
      if (_info) {
        //其他放到数组最后
        let other = {}
        _info.forEach((item, index) => {
          if (item.name == '99') {  //其他
            other = item;
            _info.splice(index, 1)
            return;
          }
        })
        _info.push(other)
        //其他放到数组最后
        for (let item of _info) {
          if (codes.includes(item.name)) {
            let _obj = {}, endObjs = {}
            if (item.data) {
              for (let sys of item.data) {
                _obj[sys.status] = sys.ccsum + '';
              }
            }
            for (let value of status) {
              if (!endObjs[this.statusMap[value]]) {
                endObjs[this.statusMap[value]] = _obj[value] ? _obj[value] : '0'
              }

            }
            tdMap.push({ name: item.typeName, typecode: item.name, data: endObjs });
            includesCodes.push(item.name);
          }
        }
        // 无数据补充数据
        if (includesCodes.length !== codes.length) {
          for (let v of codes) {
            if (!includesCodes.includes(v)) {
              let _objs = {
                name: this.translateMap[v], typecode: v
              };
              let _datas = {}
              for (let v of status) {
                _datas[this.statusMap[v]] = 0
              }
              _objs.data = _datas;
              this.tdMap.push(_objs)
            } else {
              for (let item of tdMap) {
                if (item.typecode === v) {
                  this.tdMap.push(item)
                }
              }
            }
          }
        } else {
          this.tdMap = tdMap
        }
      } else {
        for (let v of codes) {
          let _objs = {
            name: this.translateMap[v], typecode: v
          };
          let _datas = {}
          for (let v of status) {
            _datas[this.statusMap[v]] = 0
          }
          _objs.data = _datas;
          this.tdMap.push(_objs)
        }
      }
      window.console.log(this.tdMap, 'this.tdMap');
    }
  }
}
</script>

<style scoped lang="scss">
.top-left-style {
  width: 100%;
  height: 100%;
  position: relative;

  .main-style {
    width: 100%;
    height: 100%;
    position: relative;

    .left-details {
      width: 100%;
      height: 100%;
      // height: 45%;
      float: left;

      .details-inline {
        height: 24.5%;
        display: flex;
        width: 100%;
        // padding: 0 10px;
        padding: 0 10px 0 20px;
        // box-sizing: border-box;
        background: url("../../../assets/img/远程框4.png") no-repeat top center;
        background-size: 100% 100%;
        .inline-title {
          // width: 40px;
          // flex: 1.5;
          flex: 1;
          color: #fff;
          font-size: 16px;
          // padding: 8px 10px;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .inline-main {
          flex: 3;
          // width: calc(100% - 60px);
          font-size: 16px;
          display: flex;
          margin-top: 8px;
          // padding-left: 20px;
          .main-info {
            display: inline-block;
            // padding: 5px 0;
            // padding-left: 5px;
            display: flex;
            flex-direction: column;
            // width: calc(50% - 6px);
            flex: 1;
            .keyInfo {
              display: flex;
              font-size: 14px;

              height: 50%;
              height: 40%;
              justify-content: center;
              align-items: flex-end;
            }

            .valueInfo {
              // display: flex;
              // align-items: center;
              width: 100%;
              height: 40%;
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }

          .main-info:nth-child(1) {
            color: #fff;
            .valueInfo {
              color: #00e7ff;
            }
          }

          .main-info:nth-child(2) {
            color: #fff;
            .valueInfo {
              color: #00ffd4;
            }
          }

          .main-info:nth-child(3) {
            color: #fff;
            .valueInfo {
              color: #86ff80;
            }
          }
        }
      }
    }
  }
}
</style>