<template>
  <div class="map-style">
    <el-dialog
      :title="title"
      center
      :show-close="true"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      @closed="closed"
      width="26%"
    >
      <div class="conBox" v-loading="loading">
        <div class="left">
          <table border v-cloak>
            <tr class="first">
              <td colspan="2">营销专业管理室</td>
            </tr>
            <tr>
              <td>登录人数</td>
              <td class="first2">{{ loginNo1 || 0 }}</td>
            </tr>
            <tr>
              <td>非抢修工单总量</td>
              <td class="first2">
                {{
                  list1[0].IS_OVERTIME_COUNT ||
                  0 + list1[1].IS_OVERTIME_COUNT ||
                  0
                }}
              </td>
            </tr>
            <tr>
              <td>未超时</td>
              <td class="first2">{{ list1[0].IS_OVERTIME_COUNT || 0 }}</td>
            </tr>
            <tr>
              <td>已超时</td>
              <td class="first2">{{ list1[1].IS_OVERTIME_COUNT || 0 }}</td>
            </tr>
          </table>
        </div>
        <div class="right">
          <table border>
            <tr class="first">
              <td colspan="2">配网抢修指挥班</td>
            </tr>
            <tr>
              <td>登录人数</td>
              <td class="first2">{{ loginNo2 || 0 }}</td>
            </tr>
            <tr>
              <td>抢修工单总量</td>
              <td class="first2">
                {{
                  list2[0].IS_OVERTIME_COUNT ||
                  0 + list2[1].IS_OVERTIME_COUNT ||
                  0
                }}
              </td>
            </tr>
            <tr>
              <td>未超时</td>
              <td class="first2">{{ list2[0].IS_OVERTIME_COUNT || 0 }}</td>
            </tr>
            <tr>
              <td>已超时</td>
              <td class="first2">{{ list2[1].IS_OVERTIME_COUNT || 0 }}</td>
            </tr>
          </table>
        </div>
        <div class="clear"></div>
      </div>
    </el-dialog>
    <div class="charts-style" :id="id" ref="refMap"></div>
  </div>
</template>

<script>
import echarts from 'echarts';
import JSON from './map.json';
import 'echarts-gl';
import qs from 'qs';
import Interface from "../../../base/mixin/service-index-control";

export default {
  name: "remoteWorkMap",
  mixins: [Interface],
  data() {
    return {
      id: Math.random() + '_',
      charts: null,
      area: '',//点击地图区域
      code: '',//点击地图区域的编码
      dialogVisible: false,
      list1: [{ IS_OVERTIME_COUNT: 0 }, { IS_OVERTIME_COUNT: 0 }],//营销专业
      loginNo1: '',//营销专业登录人数
      list2: [{ IS_OVERTIME_COUNT: 0 }, { IS_OVERTIME_COUNT: 0 }],//配网抢修
      loginNo2: '',//配网抢修登录人数
      windows: false,
      loading: true,
      title: '供电分公司',
      mapData: [],
      mapDataMax: 0
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.init();
    this.getMapDetail()
  },
  methods: {
    /**
     * 深度拷贝公用方法
     *
     * **/
    deepCopy(obj1, obj2) {
      for (var key in obj1) {
        var item = obj1[key]
        if (item instanceof Array) { //深度遍历，如果对象里面是数组
          obj2[key] = []
          this.deepCopy(item, obj2[key])
        } else if (item instanceof Object) { //深度遍历，如果对象里面是对象
          obj2[key] = {}
          this.deepCopy(item, obj2[key])
        } else {   //基本数据类型拷贝
          obj2[key] = obj1[key];
        }
      }
    },
    closed() {
      this.area = ''
      this.title = '供电分公司'
      this.loading = 'true'
    },
    getDetail() {
      window.console.log(this.area, 'this.area');
      let $this = this;
      this.dialogVisible = true
      this.getAllAreaCode(qs.stringify({})).then(res => {
        $this.title = this.area + '供电分公司'
        Object.keys(res).forEach(key => {
          if ($this.area.indexOf(res[key]) != -1) {
            $this.code = key
          }
        })
        // if (this.area === '主城区') {
        //   this.code = '主城区'
        // }
        this.getPowerDetail()
      }).catch(() => {
        // this.zrgsRes=true;
      });
    },
    /** 获取地图数据  */
    getMapDetail() {
      this.getOrgTranslate({}).then(res1 => {
        this.orgTranslate = res1;
        this.getBusinessMapSummaryData(qs.stringify({
          timeStr: this.$parent.dateParams,
        })
        ).then(res2 => {
          this.mapData = this.translateMap(res2, this.orgTranslate);
          this.mapData.sort((a, b) => {
            return a.value - b.value
          })
          this.mapDataMax = this.mapData[this.mapData.length - 1].value
          window.console.log(this.mapDataMax, 'this.mapDataMax');
          this.init();
        });
      })
    },

    /** 转移地图编码*/
    translateMap(data, transData) {
      let backData = []
      let transDealData = []
      for (let key in transData) {
        transDealData.push({
          name: transData[key],
          value: key,
          indexOf: false
        })
      }
      data.forEach((e, i) => {
        transDealData.forEach(e2 => {
          if (e2.value === e.orgNo) {
            backData.push({
              name: e2.name,
              value: data[i]['sum(cc)'] || 0
            })
            e2.indexOf = true
          }
        })
      })
      // data.forEach(e => {
      //   if (e.orgNo === '主城区') {
      //     backData.push({
      //       name: '主城区',
      //       value: e['sum(cc)'] || 0
      //     })
      //   }
      // })
      transDealData.forEach(e => {
        if (!e.indexOf) {
          backData.push({
            name: e.name,
            value: 0
          })
        }
      })
      let isRongChang = false
      let isLiangPing = false
      backData.forEach(e => {
        if (e.name === '荣昌') {
          isRongChang = true
          return
        }
        if (e.name === '梁平') {
          isLiangPing = true
          return

        }
      })
      if (!isRongChang) {
        backData.push(
          {
            name: '荣昌',
            value: 0,
          })
      }
      if (!isLiangPing) {
        backData.push(
          {
            name: '梁平',
            value: 0,
          })
      }

      return backData
    },
    getPowerDetail() {
      this.getPowerCompany(qs.stringify({
        timeStr: this.$parent.dateParams,
        orgNo: this.code,
      }
      )).then(res => {
        if (res && res.length !== 0) {
          res.forEach((item) => {
            if (item.IS_REPAIR == 1) {
              this.deepCopy(item.COUNT, this.list2)
              this.loginNo2 = item.LOGIN_COUNT

            } else {
              this.deepCopy(item.COUNT, this.list1)
              this.loginNo1 = item.LOGIN_COUNT
            }
          })
          window.console.log(this.list1, this.list2, 'list2');
          this.loading = false;
          // this.windows =true
        }
      });
    },
    init() {
      let params1 = {
        names: [
          "市区",
          "大渡口",
          "九龙",
          "沙坪",
          "渝中",
        ],
        properties: {
          name: "市区",
          cp: [106.368212,
            29.624503]
        }
      }
      let params2 = {
        names: [
          "江北",
          "渝北",
        ],
        properties: {
          name: "市北",
          cp: [106.746924,
            29.810209]
        }
      }
      let params3 = {
        names: [
          "南岸",
          "巴南",
        ],
        properties: {
          name: "市南",
          cp: [106.751731,
            29.371851]
        }
      }
      let params4 = {
        names: [
          "荣昌",
          "永川",
        ],
        properties: {
          name: "永川",
          cp: [
            // 105.872859,
            // 29.290183
            105.702859,
            29.300183
          ]
        }
      }
      let params5 = {
        names: [
          "梁平",
          "万州",
        ],
        properties: {
          name: "万州",
          cp: [
            // 108.406819,
            // 30.704054
            108.056819,
            30.704054
          ]
        }
      }
      this.mergeProvinces(JSON, params1.names, params1.properties)
      this.mergeProvinces(JSON, params2.names, params2.properties)
      this.mergeProvinces(JSON, params3.names, params3.properties)
      this.mergeProvinces(JSON, params4.names, params4.properties)
      this.mergeProvinces(JSON, params5.names, params5.properties)
      let chartsDom = document.getElementById(this.id);
      this.charts = echarts.init(chartsDom);
      echarts.registerMap('重庆', JSON);
      var $this = this;
      this.charts.on('click', function (params) {
        window.console.log(params, 'params');
        $this.area = params.name
        let isinside = $this.$refs.refMap.children[0].style.cursor;
        if ($this.area !== '' && $this.area !== '荣昌' && $this.area !== '梁平' && $this.area && isinside == "pointer") {
          $this.getDetail()
        }
      });
      this.charts.setOption({
        visualMap: {
          min: 0,
          max: $this.mapDataMax,
          text: ['多', '少'],
          realtime: false,
          calculable: false,
          bottom: 20,
          inRange: {
            color: ['#237FE7', '#14EFC3']
          },
          textStyle: {
            color: '#fff'
          }
        },
        geo: [
          {
            map: '重庆',
            roam: false,
            zoom: 1.1,
            zlevel: 2,
            z: 1,
            aspectScale: 1,
            label: {
              show: true,
              color: '#fff',
              fontFamily: 'Noto Sans SC',
              emphasis: {
                color: '#fff',
              },
              position: {
                // bottom: '-100px',
              },
              // distance: 100
            },
            regions: [      //对不同的区块进行着色
              // {
              //   name: '主城区', //区块名称
              //   label: {
              //     color: '#FFFB50',
              //   }
              // },
              {
                name: '潼南', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '合川', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '铜梁', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '大足', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '璧山', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '永川', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '江津区', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '綦南', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '南川', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },
              {
                name: '北碚', //区块名称
                label: {
                  color: '#FFFB50',
                }
              },

              {
                name: '市区', //区块名称
                label: {
                  color: '#FFFB50',

                },
              },
              {
                name: '市南', //区块名称
                label: {
                  color: '#FFFB50',

                },
              },
              {
                name: '市北', //区块名称
                label: {
                  color: '#FFFB50',

                },
              },
              {
                name: '荣昌', //区块名称
                label: {
                  show: false,

                },
                emphasis: {
                  label: {
                    show: false,
                  },
                }

              },
              {
                name: '梁平', //区块名称
                label: {
                  show: false,
                },
                emphasis: {
                  label: {
                    show: false,
                  },
                }

              },

            ],
            itemStyle: {
              //   normal: {
              //     areaColor: '#237fe7',
              //     borderWidth: 1,
              //     borderType: 'dotted',
              //     borderColor: '#003C80',

              //   },
              emphasis: {
                color: '#00D9FF',
              }
            },

          },
          {
            map: '重庆',
            roam: false,
            zoom: 1.1,
            zlevel: 1,
            z: 1,
            aspectScale: 1,
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: false
              }
            },
            itemStyle: {
              normal: {
                borderColor: '#013a67',
                borderWidth: 2,
                areaColor: '#0aade4',
                shadowColor: 'rgba(1,58,101, 1)',
                shadowBlur: 0,
                shadowOffsetX: -1,
                shadowOffsetY: 21

              },
              emphasis: {
                borderWidth: 1,
                areaColor: '#00194d',
                borderColor: '#00194d'
              }
            }
          },
        ],
        series: [
          {
            name: '',
            type: 'map',
            mapType: '重庆', // 自定义扩展图表类型
            geoIndex: 0,

            data: $this.mapData,
            // 自定义名称映射
          }
        ]
      })
    },
    //合并地图
    mergeProvinces(geojson, names, properties) {
      var features = geojson.features;
      var polygons = [];
      // 将指定省的polygon保存下来，并将省的数据从地图中删除
      for (var i = 0, iLen = names.length; i < iLen; i++) {
        for (var j = 0, jLen = features.length; j < jLen; j++) {
          if (features[j].properties.name == names[i]) {
            polygons = polygons.concat(features[j].geometry.coordinates);
            features.splice(j, 1);
            break;
          }
        }
      }
      // 构建新的合并区域
      var feature = {
        type: "Feature",
        id: "" + features.length,
        properties: {
          name: properties.name || ""
        },
        geometry: {
          type: "MultiPolygon",
          coordinates: polygons
        }
      };
      if (properties.cp) {
        feature.properties.cp = properties.cp;
      }

      // 将新的合并区域添加到地图中
      features.push(feature);
    },
  }
}
</script>

<style scoped lang="scss">
[v-cloak] {
  display: none;
}

.map-style {
  width: 100%;
  height: 100%;
  position: relative;

  .charts-style {
    width: 100%;
    height: 100%;
  }
}

/deep/ .el-dialog {
  background-color: rgba(0, 0, 0, 0);
}

/deep/ .el-dialog__header {
  padding: 10px;
  background: linear-gradient(to top, #3c7f97, #68bdc1);
}

/deep/ .el-dialog__title {
  color: #232323;
}

/deep/ .el-dialog .el-dialog__body {
  padding: 0px;
}

.conBox {
  width: 100%;
  background-color: rgba(60, 127, 151, 0.6);
  padding-top: 10px;

  table {
    height: 100%;
    width: 100%;
    border-color: #68bdc1;
    color: #fff;
    text-align: center;

    td {
      padding: 4px;
    }

    .first {
      background-color: #092b57;
    }

    .first2 {
      background-color: #2b4b6f;
    }
  }

  .left {
    width: 47%;
    min-height: 160px;
    float: left;
  }

  .right {
    width: 47%;
    min-height: 160px;
    float: right;
  }

  .clear {
    clear: both;
  }
}
</style>